import React from 'react';

function List(props) {
	const { data, titleColor, titlePosition, descriptionColor, descriptionPosition } = props;
	return(
		<>
      {
        data.map(item => {
          return(
            <div key={item.id} className="container">
              <p className="title">{`â€¢ ${item.title}${item.date?" | "+item.date:""}`}</p>
              {
                item.description &&
                <p className="description">{item.description}</p>
              }
            </div>
          )
        })
      }

      <style jsx>{`
        .container{
          width: 100%;
        }
        .title{
          font-weight: bold;
          color: ${titleColor || "#fff"};
          text-align: ${titlePosition || "left"};
        }
        .description{
          margin: 10px 10px;
          color: ${descriptionColor || "#fff"};
          text-align: ${descriptionPosition || "justify"};
        }
      `}</style>
    </>
	);
}

export default List;